"use client"

import { DashboardNav } from "@/components/dashboard-nav"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Separator } from "@/components/ui/separator"
import { Settings, User, Building, Lock, Save, ArrowLeft } from "lucide-react"
import { useState, useEffect } from "react"
import { withAuth } from "@/components/with-auth"
import { toast } from "sonner"
import Link from "next/link"

function SettingsPage({ user }: { user: any }) {
  const [isLoading, setIsLoading] = useState(true)
  const [isSaving, setIsSaving] = useState(false)
  const [isChangingPassword, setIsChangingPassword] = useState(false)
  
  // Profile data
  const [profile, setProfile] = useState({
    name: "",
    email: "",
    role: "",
    businessName: "",
    businessType: "",
    businessEmail: "",
    businessPhone: "",
    businessAddress: "",
  })

  // Get dashboard path based on role
  const getDashboardPath = () => {
    if (!user?.role) return "/dashboard"
    if (user.role === "charity") return "/dashboard/charity"
    if (user.role === "farmer") return "/dashboard/farmer"
    return "/dashboard"
  }
  
  // Password change
  const [passwords, setPasswords] = useState({
    currentPassword: "",
    newPassword: "",
    confirmPassword: "",
  })

  // Fetch user profile on mount
  useEffect(() => {
    fetchProfile()
  }, [])

  const fetchProfile = async () => {
    try {
      const storedUser = JSON.parse(localStorage.getItem("user") || "{}")
      if (!storedUser.userId) {
        toast.error("Please log in again")
        return
      }

      const response = await fetch("/api/user/profile", {
        headers: {
          "x-user-id": storedUser.userId,
        },
      })

      if (response.ok) {
        const data = await response.json()
        setProfile({
          name: data.name || "",
          email: data.email || "",
          role: data.role || "",
          businessName: data.business?.name || "",
          businessType: data.business?.type || "",
          businessEmail: data.business?.email || "",
          businessPhone: data.business?.phone || "",
          businessAddress: data.business?.address || "",
        })
      } else {
        toast.error("Failed to load profile")
      }
    } catch (error) {
      console.error("Error fetching profile:", error)
      toast.error("Failed to load profile")
    } finally {
      setIsLoading(false)
    }
  }

  const handleSaveProfile = async () => {
    setIsSaving(true)
    try {
      const storedUser = JSON.parse(localStorage.getItem("user") || "{}")
      if (!storedUser.userId) {
        toast.error("Please log in again")
        return
      }

      const response = await fetch("/api/user/profile", {
        method: "PATCH",
        headers: {
          "Content-Type": "application/json",
          "x-user-id": storedUser.userId,
        },
        body: JSON.stringify({
          name: profile.name,
          businessName: profile.businessName,
          businessPhone: profile.businessPhone,
          businessAddress: profile.businessAddress,
        }),
      })

      if (response.ok) {
        toast.success("Profile updated successfully!")
        // Update localStorage with new name and business name
        const updatedUser = { 
          ...storedUser, 
          name: profile.name,
          businessName: profile.businessName 
        }
        localStorage.setItem("user", JSON.stringify(updatedUser))
        
        // Dispatch custom event to notify navbar to refresh
        window.dispatchEvent(new CustomEvent("profileUpdated", { 
          detail: { businessName: profile.businessName } 
        }))
      } else {
        const errorData = await response.json()
        toast.error(errorData.error || "Failed to update profile")
      }
    } catch (error) {
      console.error("Error saving profile:", error)
      toast.error("Failed to save profile")
    } finally {
      setIsSaving(false)
    }
  }

  const handleChangePassword = async () => {
    // Validate passwords
    if (!passwords.currentPassword || !passwords.newPassword || !passwords.confirmPassword) {
      toast.error("Please fill in all password fields")
      return
    }

    if (passwords.newPassword !== passwords.confirmPassword) {
      toast.error("New passwords do not match")
      return
    }

    if (passwords.newPassword.length < 6) {
      toast.error("New password must be at least 6 characters")
      return
    }

    setIsChangingPassword(true)
    try {
      const storedUser = JSON.parse(localStorage.getItem("user") || "{}")
      if (!storedUser.userId) {
        toast.error("Please log in again")
        return
      }

      const response = await fetch("/api/user/password", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "x-user-id": storedUser.userId,
        },
        body: JSON.stringify({
          currentPassword: passwords.currentPassword,
          newPassword: passwords.newPassword,
        }),
      })

      if (response.ok) {
        toast.success("Password changed successfully!")
        setPasswords({ currentPassword: "", newPassword: "", confirmPassword: "" })
      } else {
        const errorData = await response.json()
        toast.error(errorData.error || "Failed to change password")
      }
    } catch (error) {
      console.error("Error changing password:", error)
      toast.error("Failed to change password")
    } finally {
      setIsChangingPassword(false)
    }
  }

  const getRoleDisplay = (role: string) => {
    const roleMap: Record<string, string> = {
      ADMIN: "Administrator",
      STAFF: "Café Staff",
      PARTNER: "Partner",
    }
    return roleMap[role] || role
  }

  const getBusinessTypeDisplay = (type: string) => {
    const typeMap: Record<string, string> = {
      CAFE: "Café",
      RESTAURANT: "Restaurant", // Legacy support for existing data
      NGO: "Charity / NGO",
      FARM: "Farm",
      OTHER: "Other", // Legacy support for existing data
    }
    return typeMap[type] || type
  }

  if (isLoading) {
    return (
      <div className="min-h-screen bg-background">
        <DashboardNav />
        <main className="pt-20 px-4 pb-8">
          <div className="container mx-auto max-w-3xl">
            <div className="flex items-center justify-center py-20">
              <p className="text-muted-foreground">Loading settings...</p>
            </div>
          </div>
        </main>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-background">
      <DashboardNav />

      <main className="pt-20 px-4 pb-8">
        <div className="container mx-auto max-w-3xl">
          {/* Header */}
          <div className="mb-8">
            <Link href={getDashboardPath()} className="inline-flex items-center text-sm text-muted-foreground hover:text-foreground mb-4">
              <ArrowLeft className="w-4 h-4 mr-2" />
              Back to Dashboard
            </Link>
            <div className="flex items-center gap-3">
              <div className="w-12 h-12 rounded-xl bg-primary/10 flex items-center justify-center">
                <Settings className="w-6 h-6 text-primary" />
              </div>
              <div>
                <h1 className="font-display font-bold text-3xl text-foreground">Settings</h1>
                <p className="text-muted-foreground">Manage your account and preferences</p>
              </div>
            </div>
          </div>

          {/* Profile Section */}
          <Card className="p-6 mb-6">
            <div className="flex items-center gap-3 mb-6">
              <User className="w-5 h-5 text-primary" />
              <h2 className="font-display font-bold text-xl text-foreground">Profile Information</h2>
            </div>

            <div className="grid gap-4">
              <div className="grid md:grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="name">Full Name</Label>
                  <Input
                    id="name"
                    value={profile.name}
                    onChange={(e) => setProfile({ ...profile, name: e.target.value })}
                    placeholder="Your name"
                  />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="email">Email Address</Label>
                  <Input
                    id="email"
                    value={profile.email}
                    disabled
                    className="bg-muted"
                  />
                  <p className="text-xs text-muted-foreground">Email cannot be changed</p>
                </div>
              </div>
              
              <div className="space-y-2">
                <Label>Role</Label>
                <Input
                  value={getRoleDisplay(profile.role)}
                  disabled
                  className="bg-muted"
                />
              </div>
            </div>
          </Card>

          {/* Business Section */}
          <Card className="p-6 mb-6">
            <div className="flex items-center gap-3 mb-6">
              <Building className="w-5 h-5 text-primary" />
              <h2 className="font-display font-bold text-xl text-foreground">Business Information</h2>
            </div>

            <div className="grid gap-4">
              <div className="grid md:grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="businessName">Business Name</Label>
                  <Input
                    id="businessName"
                    value={profile.businessName}
                    onChange={(e) => setProfile({ ...profile, businessName: e.target.value })}
                    placeholder="Your business name"
                  />
                </div>
                <div className="space-y-2">
                  <Label>Business Type</Label>
                  <Input
                    value={getBusinessTypeDisplay(profile.businessType)}
                    disabled
                    className="bg-muted"
                  />
                </div>
              </div>

              <div className="grid md:grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="businessEmail">Business Email</Label>
                  <Input
                    id="businessEmail"
                    value={profile.businessEmail}
                    disabled
                    className="bg-muted"
                  />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="businessPhone">Phone Number</Label>
                  <Input
                    id="businessPhone"
                    value={profile.businessPhone}
                    onChange={(e) => setProfile({ ...profile, businessPhone: e.target.value })}
                    placeholder="Contact phone number"
                  />
                </div>
              </div>

              <div className="space-y-2">
                <Label htmlFor="businessAddress">Address</Label>
                <Input
                  id="businessAddress"
                  value={profile.businessAddress}
                  onChange={(e) => setProfile({ ...profile, businessAddress: e.target.value })}
                  placeholder="Business address"
                />
              </div>
            </div>

            <div className="mt-6">
              <Button 
                onClick={handleSaveProfile} 
                disabled={isSaving}
                className="bg-primary hover:bg-primary/90"
              >
                <Save className="w-4 h-4 mr-2" />
                {isSaving ? "Saving..." : "Save Changes"}
              </Button>
            </div>
          </Card>

          {/* Password Section */}
          <Card className="p-6">
            <div className="flex items-center gap-3 mb-6">
              <Lock className="w-5 h-5 text-primary" />
              <h2 className="font-display font-bold text-xl text-foreground">Change Password</h2>
            </div>

            <div className="grid gap-4">
              <div className="space-y-2">
                <Label htmlFor="currentPassword">Current Password</Label>
                <Input
                  id="currentPassword"
                  type="password"
                  value={passwords.currentPassword}
                  onChange={(e) => setPasswords({ ...passwords, currentPassword: e.target.value })}
                  placeholder="Enter current password"
                />
              </div>

              <Separator />

              <div className="grid md:grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="newPassword">New Password</Label>
                  <Input
                    id="newPassword"
                    type="password"
                    value={passwords.newPassword}
                    onChange={(e) => setPasswords({ ...passwords, newPassword: e.target.value })}
                    placeholder="Enter new password"
                  />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="confirmPassword">Confirm New Password</Label>
                  <Input
                    id="confirmPassword"
                    type="password"
                    value={passwords.confirmPassword}
                    onChange={(e) => setPasswords({ ...passwords, confirmPassword: e.target.value })}
                    placeholder="Confirm new password"
                  />
                </div>
              </div>
            </div>

            <div className="mt-6">
              <Button 
                onClick={handleChangePassword} 
                disabled={isChangingPassword}
                variant="outline"
              >
                <Lock className="w-4 h-4 mr-2" />
                {isChangingPassword ? "Changing..." : "Change Password"}
              </Button>
            </div>
          </Card>
        </div>
      </main>
    </div>
  )
}

export default withAuth(SettingsPage, ["staff", "admin", "charity", "farmer"])



